#tiles {
  height: 100vh;
  width: auto;
  z-index: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  grid-template-rows: repeat(var(--rows), 1fr);
  padding: 1px 2px 2px 1px;
  display: grid;
  position: relative;
  left: 0;
}

.tile {
  cursor: pointer;
  opacity: .95;
  position: relative;
}

.tile:before {
  background-color: var(--primary-bg-color);
  content: "";
  position: absolute;
  inset: 1.5px;
}

.tile:hover {
  opacity: .5;
}

main {
  background: linear-gradient(to right, #9477f5, #f58a78, #9477f5) 0 0 / 200%;
  animation: 14s linear infinite background-pan;
  top: 0;
}

@keyframes background-pan {
  from {
    background-position: 0%;
  }

  to {
    background-position: -200%;
  }
}

@keyframes fly {
  0% {
    transform: rotate(0)translate(0)rotate(0);
  }

  50% {
    transform: rotate(1turn)translate(100px)rotate(-1turn);
  }

  100% {
    transform: rotate(0)translate(0)rotate(0);
  }
}

@keyframes flyPlus {
  0% {
    transform: rotate(0)translate(0)rotate(0);
  }

  50% {
    transform: rotate(-1turn)translate(25vh)rotate(1turn);
  }

  100% {
    transform: rotate(0)translate(0)rotate(0);
  }
}

.wrapper {
  overflow: hidden;
}

.one {
  width: 50vw;
  height: 50vh;
  z-index: 0;
  filter: blur(100px);
  background-color: #9477f5;
  border-radius: 100%;
  animation: 12s linear infinite fly;
  position: absolute;
  top: 15rem;
  left: -5rem;
}

.two {
  width: 40vw;
  height: 70vh;
  filter: blur(100px);
  background-color: #f58a78;
  border-radius: 25%;
  animation: 16.333s linear infinite flyPlus;
  position: absolute;
  bottom: 20rem;
  right: -8rem;
}

.three {
  width: 30vw;
  height: 30vh;
  filter: blur(100px);
  background-color: #9477f5;
  border-radius: 100%;
  animation: 16s linear infinite flyPlus;
  position: absolute;
  bottom: 16rem;
  right: 10rem;
}

